<template>
  <div class="wrapper wrapper-content">

    <div class="ibox float-e-margins">
      <div class="ibox-title">
        <h5>用户管理</h5>
      </div>

      <div class="ibox-content">
        <div class="row">
          <div class="searchOption">
            <label class="control-label">用户状态</label>
            <select class="form-control" v-model="status">
              <option value="-1">全部</option>
              <option value="1">未审核</option>
              <option value="2">正常</option>
            </select>
          </div>
          <button type="button" class="btn btn-warning search" @click="search">查询</button>
        </div>

        <div class="text-left" v-if="operation.add">
          <button class="btn btn-primary" @click="add()">新增</button>
          <button v-if="operation.edit" class="btn btn-warning" @click="selectUsers()">勾选用户分组</button>
        </div>

        <table class="table table-bordered text-center">
          <thead>
          <tr>
            <th></th>
            <th>编号</th>
            <th>真实姓名</th>
            <th>用户名</th>
            <th>联系电话</th>
            <th>创建时间</th>
            <th>操作</th>
          </tr>
          </thead>
          <tbody>
          <tr v-for="(item, index) of items" :key="item.id">
            <td>
              <div class="checkbox i-checks">
                <label>
                  <input name="select" type="checkbox" :value="item.id"> <i></i></label>
              </div>
            </td>
            <td>{{ index + 1 }}</td>
            <td>{{ item.name }}</td>
            <td>{{ item.username }}</td>
            <td>{{ item.phone }}</td>
            <td>{{ item.created_at }}</td>
            <td>
              <template v-if="item.username != 'admin'">
                <button v-if="operation.edit && item.status == 1" type="button" class="btn btn-sm btn-warning" @click="check(item)">审核通过</button>
                <button v-if="operation.edit" type="button" class="btn btn-sm btn-primary" @click="edit(item)">编辑</button>
                <button v-if="operation.edit" type="button" class="btn btn-sm btn-danger" @click="editPassword(item)">修改密码</button>
                <delete v-if="operation.del && user.id != item.id" @delConfirm="delConfirm" v-bind:item="item"></delete>
              </template>
              <template v-else>
                <button v-if="user.username == 'admin' && user.id == 1" type="button" class="btn btn-sm btn-primary" @click="edit(item)">编辑</button>
              </template>
            </td>
          </tr>
          </tbody>
        </table>
        <vue-pagination :current-page="page" :total="total" :page-size="size" @current-change="pageChange" theme="gray"></vue-pagination>

      </div>
    </div>

    <!-- model -->
    <div class="modal inmodal fade" id="Modal" tabindex="-1" role="dialog"  aria-hidden="true">
      <div class="modal-dialog modal-md">
        <div class="modal-content">
          <div class="modal-header">
            <button type="button" class="close" data-dismiss="modal"><span aria-hidden="true">&times;</span><span class="sr-only">Close</span></button>
            <h4 class="modal-title">为勾选用户选择分组</h4>
          </div>

          <form id="form" class="form-horizontal" @submit.prevent="submit">
            <div class="modal-body">
              <div class="row">
                <div class="form-group">
                  <label class="col-sm-3 control-label">用户组</label>
                  <div class="col-sm-8">
                    <select class="form-control" v-model="selectRole">
                      <option v-for="role of roles" :value="role.id">{{ role.name }}</option>
                    </select>
                  </div>
                </div>
              </div>
            </div>

            <div class="modal-footer">
              <b class="btn btn-white" data-dismiss="modal">关闭</b>
              <button type="submit" class="btn btn-primary">提交</button>
            </div>
          </form>

        </div>
      </div>
    </div>
    <!-- model end -->

  </div>
</template>

<script type="text/ecmascript-6">
import Cache from '@/assets/cache'
import tabsService from '@/assets/template/tabs'
import utilizeService from '@/assets/template/utilize'
import dataService from '@/assets/method/data'
import Service from '@/assets/service/service'
export default {
  name: 'User',
  data () {
    return {
      items: [],
      roles: [],
      selectRole: null,
      total: 0,
      page: 1,
      size: 10,
      status: -1,
      user: Cache.getUser(),
      operation: {}
    }
  },
  methods: {
    search () {
      this.getDataTables()
    },
    pageChange (page) {
      this.getDataTables(page)
    },
    getDataTables (page = 1) {
      const request = {
        page: page
      }
      if (this.status > -1) {
        request['status'] = this.status
      }
      Service.Users.get(request).then(response => {
        if (response.code == 200) {
          this.page = page
          this.items = response.data.data
          this.total = response.data.total
          setTimeout(() => utilizeService.iCheck(), 200)
        }
      })
    },
    add () {
      tabsService.addDetailsTab('/views/user/new', '用户-新增')
    },
    edit (item) {
      tabsService.addDetailsTab('/views/user/' + item.id, '用户-编辑')
    },
    editPassword (item) {
      tabsService.addDetailsTab('/editUserPassword/' + item.id, '修改密码-' + item.name)
    },
    check (item) {
      swal({
        title: '确定审核通过吗？',
        text: '确定后用户将可以登录系统，请谨慎操作！',
        type: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#DD6B55',
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        closeOnConfirm: false
      }, () => {
        Service.Users.edit(item.id, { type: 'status', status: 2 }).then(response => {
          if (response.code == 200) {
            swal('审核成功！', '该用户已经正常启用', 'success')
            this.getDataTables(this.page)
          } else {
            toastr.error(response.msg)
          }
        })
      })
    },
    delConfirm (item) {
      Service.Users.del(item.id).then(response => {
        if (response.code == 200) {
          this.getDataTables()
        }
      })
    },
    selectUsers () {
      const ids = dataService.getChecked('select')
      if (ids.length == 0) {
        toastr.warning('请勾选用户')
        return
      }
      this.selectRole = null
      $('#Modal').modal('show')
    },
    submit () {
      if (!this.selectRole) {
        toastr.warning('请选择分组')
        return
      }
      Service.UpdateUsersRoles({
        r_id: this.selectRole,
        ids: dataService.getChecked('select')
      }).then(response => {
        if (response.code == 200) {
          toastr.success('修改成功')
          $('#Modal').modal('hide')
        } else {
          toastr.error(response.msg)
        }
      })
    }
  },
  created () {
    if (Cache.hasPermission('post_user')) {
      this.operation.add = true
    }
    if (Cache.hasPermission('update_user')) {
      this.operation.edit = true
    }
    if (Cache.hasPermission('delete_user')) {
      this.operation.del = true
    }
    Service.Roles.get().then(response => {
      if (response.code == 200) {
        this.roles = response.data
        console.log(this.roles)
      }
    })
    this.getDataTables()
  },
  mounted () {
  }
}
</script>

<style scoped>
  .searchOption {
    display: inline-block;
    float: left;
    margin-bottom: 10px;
  }
  .searchOption label {
    display: inline-block;
    float: left;
    margin: 8px 15px 0 15px;
  }
  .searchOption select {
    display: inline-block;
    float: left;
    width: 150px;
  }
  .search {
    margin-left: 10px;
  }
  .modal-header, .modal-footer {
    padding: 10px;
  }
</style>



